<template>
  <div class="sign-up">
      <h4>Let's create a new account!</h4>
    <!--     <h5 style="display: inline-flex; margin-right: 3%">Full Name: </h5><input type="text" ref="name" class="form-control" style="display: inline-flex" v-model="user.name" placeholder="Full Name"><br>
        <h5 style="display: inline-flex; margin-right: 3%">E-mail: </h5><input type="text" ref="email" class="form-control" style="display: inline-flex" v-model="user.email" placeholder="E-mail"><br>
        <h5 style="display: inline-flex; margin-right: 3%">Password: </h5><input type="password" ref="password1" class="form-control" style="display: inline-flex" v-model="user.password1" placeholder="Password"><br>
        <h5 style="display: inline-flex; margin-right: 3%">Confrim Password: </h5><input type="password" ref="password2" class="form-control" style="display: inline-flex" v-model="user.password2" placeholder="Confirm password"><br>
        <h5 style="display: inline-flex; margin-right: 3%">Phone Number: </h5><input type="text" ref="phone" class="form-control" style="display: inline-flex" v-model="user.phone" placeholder="Phone number"><br> -->
        <div class="form-group">
            <label class="control-label">Full Name</label>
            <p class="control">
              <input ref="name" name="name" v-model="user.name"  v-validate="'required|alpha'" class="form-control" :class="{'input': true, 'is-danger': errors.has('name') }" type="text" placeholder="Full Name">
              <span v-show="errors.has('name')" class="help is-danger" style="color: red; font-size: 14px">{{ errors.first('name') }}</span>
            </p>
        </div>
        <div class="column is-12">
          <label class="control-label">E-mail</label>
          <p class="control">
            <input name="email" class="form-control" v-model="user.email" v-validate="'required|email'" :class="{'input': true, 'is-danger': errors.has('email')}"  type="text" placeholder="E-mail">
            <span v-show="errors.has('email')" class="help is-danger" style="color: red; font-size: 14px">{{ errors.first('email') }}</span>
          </p>
        </div>
        <div class="form-group">
            <label class="control-label">Password</label>
            <input ref="password1" class="form-control" v-model="user.password1" type="password" placeholder="Password">
        </div>
        <div class="form-group">
            <label class="control-label">Confirm Password</label>
            <input ref="password2" class="form-control" v-model="user.password2" type="password" placeholder="Confirm Password">
        </div>
        <div class="form-group">
            <label class="control-label">Birth Date</label>
            <p class="control">
              <input ref="Birthdate" name="Birthdate" v-model="user.Birthdate" v-validate="'required|date_format:YYYY-MM-DD'" class="form-control" :class="{'input': true, 'is-danger': errors.has('Birthdate') }" type="date">
              <span v-show="errors.has('Birthdate')" class="help is-danger" style="color: red; font-size: 14px">{{ errors.first('Birthdate') }}</span>
            </p>
        </div>
        <div class="form-group">
            <label class="control-label">Facebook url</label>
            <p class="control">
              <input ref="facebook" name="facebook" v-model="user.facebook" v-validate="'required|url'" class="form-control" :class="{'input': true, 'is-danger': errors.has('facebook') }" type="text" placeholder="Facebook">
              <span v-show="errors.has('facebook')" class="help is-danger" style="color: red; font-size: 14px">{{ errors.first('facebook') }}</span>
            </p>
        </div>
        <button class="btn btn-primary" @click="validateBeforeSubmit">Sign Up</button>
        <span>or go back to <router-link to="/login">login</router-link>.</span>
  </div>
</template>

<script>
import firebase from 'firebase';
import { db } from '@/firebaseConfig'
import Vue from 'vue';
import VeeValidate from 'vee-validate';

Vue.use(VeeValidate);

var md5 = require('md5');
let usersRef = db.ref('users');

  export default {
    name: 'signUp',
    data() {
      return {
        user: {
          name: '',
          email: '',
          password1: '',
          password2: '',
          Birthdate: '',
          facebook:''
        }
      }
    },
    methods: {
      validateBeforeSubmit() {
          this.$validator.validateAll().then(result => {
            if(this.user.password1 != this.user.password2){
              alert('Password mismatch');
              this.$refs.password2.focus();
              return;
            }
            else {
              this.signup();
            }
          },
          error => {
            alert('Correct them errors!');
          }
          );
      },
      signup() {
            firebase.auth().createUserWithEmailAndPassword(this.user.email, this.user.password1).then( 
                user => {
                    alert('Your accound has been created!');

                    /* add user info to database */
                    this.addUserInfo();

                    this.$router.push({ name: 'Navigation'});
                }, 
                error => {
                    var errorCode = error.code;
                    var errorMsg = error.message;
                    if (errorCode == 'auth/weak-password') {
                        alert('The password is too week');
                    } else {
                        alert('Oops.' + errorMsg);
                    }
                }
            );
      },
      addUserInfo() {
          var newUser = {
              name: this.user.name,
              email: this.user.email,
              Birthdate: this.user.Birthdate,
              facebook: this.user.facebook
          }
          var uid = md5(this.user.email);
          console.log(uid);
          db.ref('users/'+ uid).set(newUser);
      }
    }
  }
</script>

<style scoped>
    .signUp {
        margin-top: 40px;
    }
    input {
        /*margin: 5px 0;*/
        width: 40%;
       /* padding: 15px;*/
       margin-left: 30%;
    }
    button {
        margin-top: 10px;
        width: 10%;
        cursor: pointer;
    }
    span {
        display: block;
        margin-top: 20px;
        font-size: 15px;
    }

</style>


