<template>
	<div class="container">
		<nav class="navbar navbar-expand-lg navbar-light bg-light" style="background-color: #e3f2fd;">
  			<div class="collapse navbar-collapse" id="navbarNavDropdown">
    			<ul class="navbar-nav" style="margin-top:15px; width:100%">
      				<!-- <li class="nav-item active"> -->
        				<a class="nav-link"  href="#" @click="selectedComponent = 'appProfile'">Profile</a>
      				<!-- </li> -->
      				<!-- <li class="nav-item"> -->
        				<a class="nav-link"  href="#" @click="selectedComponent = 'appBookmarks'">Bookmarks</a>
      				<!-- </li> -->
      				<!-- <li class="nav-item"> -->
        				<a class="nav-link"  href="#" @click="selectedComponent = 'appAbout'">About</a>
      				<!-- </li> -->
      				<!-- <li class="nav-item"> -->
        				<a class="nav-link"  href="#" @click="logout">Logout</a>
      				<!-- </li> -->
    			</ul>
  			</div>
		</nav>

		<component :is="selectedComponent">
            {{ selectedComponent }}
        </component>
		
	</div>
</template>

<script>
	import firebase from 'firebase';
  	import { db } from '@/firebaseConfig'
	import Profile from './profile.vue';
	import Bookmarks from './bookmarker.vue';
	import About from './about.vue';

	export default {
		components: {
			appProfile: Profile,
			appBookmarks: Bookmarks,
			appAbout: About
		},
		data() {
			return {
				selectedComponent: 'appProfile'
			}
		},
		methods: {
			logout() {
        		firebase.auth().signOut().then( () => {
          			this.$router.push( { name: 'Login'});
        		})
      		}
		}
	}

</script>

<style>
	a {
		font-size: 18px;
		margin-left: 10%;
	}
	
</style>


