<template>
	<div>
		<div class="header clearfix">
        	<h3 class="text-muted">My Profile</h3>
      	</div>
      	<hr>
      	<div class="data_line">
      		<div class="font-weight-normal topic">Name: </div>
      		&nbsp;&nbsp;
      		<div class="font-weight-normal information">{{user.username}}</div>
      	</div>
      	<div class="data_line">
      		<div class="font-weight-normal topic">E-mail: </div>
      		&nbsp;&nbsp;
      		<div class="font-weight-normal information">{{user.email}}</div>
      	</div>
      	<div class="data_line">
      		<div class="font-weight-normal topic">Birth Date: </div>
      		&nbsp;&nbsp;
      		<div class="font-weight-normal information">{{user.birthday}}</div>
      	</div>
      	<div class="data_line">
      		<div class="font-weight-normal topic">Facebook url: </div>
      		&nbsp;&nbsp;
      		<div class="font-weight-normal information">{{user.facebook}}</div>
      	</div>
	</div>
</template>

<script>
	import firebase from 'firebase';
	import { db } from '@/firebaseConfig'

	var md5 = require('md5');

	export default {
		name: 'Profile',
		data() {
			return {
				user: {
					id: '',
					username: '',
					email: '',
					birthday: '',
					facebook: ''
				}
			}
		},
		created() {
			var user = firebase.auth().currentUser;
			if (user) {
      			this.user.id = user.uid;
      			this.user.username = user.displayName;
      			this.user.email = user.email;

      			var uid = md5(user.email);
      			console.log(uid);

      			db.ref('users/' + uid).once('value').then(snapshot => {
          			if (snapshot) {
            			console.log(snapshot);
            			this.user.username = snapshot.val().name;
            			this.user.birthday = snapshot.val().Birthdate;
            			this.user.facebook = snapshot.val().facebook;
          			}
      			});
			}
		}
	}
</script>

<style>
	.topic {
		font-size: 18px;
		color: #87CEEB;
		display:inline-flex;
	}
	.information {
		font-size: 18px;
		display: inline-flex;
	}
	.data_line {
		/*display:inline-flex; 
		margin-right: 62%;*/
		text-align: left;
	}
</style>


