<template>
      <div class="container">
      <div class="header clearfix">
        <h3 class="text-muted">Bookmarker</h3>
      </div>

      <div class="jumbotron">
        <h2>Bookmark Your Favorite Sites</h2>
        <form id="myForm">
          <div class="form-group">
            <label>Site Name</label>
            <input type="text" class="form-control" placeholder="Website Name" v-model="bookmark.name">
          </div>
          <div class="form-group">
            <label>Site URL</label>
            <input type="text" class="form-control" placeholder="Website URL" v-model="bookmark.web">
          </div>
          <!-- <button class="btn btn-primary" @click="submit">Submit</button> -->
        </form>
        <div>
          <button type="submit" class="btn btn-primary" @click.prevent="submit">Submit</button>
          <button class="btn btn-primary" @click="fetchData">Show Bookmark</button>
        </div>
      </div>

      <div class="row marketing">
        <div class="col-lg-12">
          <!-- <div id="bookmarksResults"></div> -->
          <ul class="list-group">
            <div class="well" v-for="b in bookmarks" :key="b.id">
              <h3>{{ b.name }} 
                  <a class="btn btn-default" target="_blank" :href="b.web">Visit</a>
                  <a class="btn btn-danger" href="#" @click="deleteBookmark(b.id)">Delete</a>
              </h3>
            </div>
          </ul>
        </div>
      </div>

      <footer class="footer">
        <p>&copy; 2016 Bookmarker, Inc.</p>
      </footer>

    </div>
</template>

<script>
  import firebase from 'firebase';
  import { db } from '@/firebaseConfig'

  var md5 = require('md5');

  export default {
    data() {
      return {
        bookmark: {
          id: '',
          name: '',
          web: ''
        },
        bookmarks: []
      }
    },
    methods: {
      submit() {
        //console.log("hh");
        // this.$http.post('https://bookmarker-98462.firebaseio.com/bookmarks.json', this.bookmark).then(response => { 
        //   console.log(response);
        // }, error => { 
        //   console.log(error);
        // });
        // this.bookmark.name = '';
        // this.bookmark.web = '';
        var user = firebase.auth().currentUser;
        var user_id = md5(user.email);
        console.log(user_id);

        this.$http.post('https://bookmarker-98462.firebaseio.com/users/'+user_id+'/bookmarks.json', this.bookmark).then(response => { 
          console.log(response);
        }, error => { 
          console.log(error);
        });
        this.bookmark.name = '';
        this.bookmark.web = '';
      },
      fetchData() {
        // this.$http.get('https://bookmarker-98462.firebaseio.com/bookmarks.json').then(response => {return response.json();})
        // .then(data => {
        //   const resultArray = [];
        //   for (let key in data) {
        //     var val = new Object()
        //     val['id'] = key
        //     val['name'] = data[key].name
        //     val['web'] = data[key].web
        //     resultArray.push(val)
        //   }
        //   this.bookmarks = resultArray;
        // });
        var user = firebase.auth().currentUser;
        var user_id = md5(user.email);

        this.$http.get('https://bookmarker-98462.firebaseio.com/users/'+user_id+'/bookmarks.json').then(response => {return response.json();})
        .then(data => {
          const resultArray = [];
          for (let key in data) {
            var val = new Object()
            val['id'] = key
            val['name'] = data[key].name
            val['web'] = data[key].web
            resultArray.push(val)
          }
          this.bookmarks = resultArray;
        });
      },
      deleteBookmark(key) {
        // this.$http.delete('https://bookmarker-98462.firebaseio.com/bookmarks/'+key+ '.json').then(response => {
        //   this.fetchData();
        //   return response;           
        // },error => {               
        //   console.log(error);
        // });
        var user = firebase.auth().currentUser;
        var user_id = md5(user.email);

        this.$http.delete('https://bookmarker-98462.firebaseio.com/users/'+user_id+'/bookmarks/'+key+'.json').then(response => {
          this.fetchData();
          return response;           
        },error => {               
          console.log(error);
        });        
      }
    }
  }
</script>


