<template>
  <div class="login">
      <h3>Log in</h3>
      <h5 style="display: inline-flex; margin-right: 3%">Email: </h5><input type="text" class="form-control" style="display: inline-flex" v-model="user.email" placeholder="Email"><br>
      <h5 style="margin-right: 3%; display: inline-flex">Password: </h5><input type="password" class="form-control" style="display: inline-flex" v-model="user.password" placeholder="Password"><br>
      <div>
        <button class="btn btn-primary" @click="login">Login</button>
        <router-link to="/signup"><button class="btn btn-secondary">Sign Up</button></router-link>
      </div>
  </div>
</template>

<script>
import firebase from 'firebase';

  export default {
    name: 'login',
    data() {
      return {
          user: {
            email: '',
            password: ''
          }
      }
    },
    methods: {
      login() {
          firebase.auth().signInWithEmailAndPassword(this.user.email, this.user.password).then(
                user => {
                    alert('User authentication successful');
                    this.$router.push({name: 'Navigation'});
                },
                err => {
                    if (err.code === 'auth/wrong-password') {
                        alert('Wrong password.');
                    } else if (err.code === 'auth/user-not-found') {
                        alert('User not found!');
                    } else if (err.code === 'auth/invalid-email') {
                        alert('Invalid email');
                    } else {
                        alert('Oops. ' + err.message);
                    }
                    
                }
            ).catch (error => {
                if (error.code === 'auth/wrong-password') {
                    alert('Wrong password')
                } else {
                    alert(error.message);
                }
            })
      }
    }
  }
</script>

<style scoped>
    .login {
        margin-top: 40px;
    }
    input {
        margin: 10px 0;
        width: 40%;
        padding: 15px;
    }
    button {
        margin-top: 20px;
        width: 10%;
        cursor: pointer;
    }
    p {
        margin-top: 40px;
        font-size: 13px;
    }
    p a {
        text-decoration: underline;
        cursor: pointer;
    }

</style>



